*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: "Mukta", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  color: rgb(0, 0, 0);
  background-color: #e6e1cd;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

.section {
  padding-block: 1rem 2rem;
}

.container-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
}

.main {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-thumb {
  background-color: #d48c8c;
}

::-webkit-scrollbar-track {
  background-color: #e6e1cd;
}

h1,
h2,
h3 {
  font-family: "Oswald", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 4.5rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
  color: rgb(0, 0, 0);
  line-height: 1.8;
}

.dark-mode {
  background-color: #242329;
}
.dark-mode .header,
.dark-mode .nav__menu,
.dark-mode .menu__hidden {
  background-color: #242329;
}
.dark-mode .title-text,
.dark-mode .title-career,
.dark-mode .nav__link,
.dark-mode .nav__logo-name,
.dark-mode .skills-subtitle,
.dark-mode .project__description-content,
.dark-mode .project__description-content ol,
.dark-mode .nav__toggle {
  color: #e6e1cd;
}
.dark-mode p {
  color: #e6e1cd;
}
.dark-mode p .slogan {
  color: rgb(255, 255, 255);
  background: linear-gradient(90deg, #242329 10%, rgba(212, 135, 138, 0.6783088235) 61%);
}
.dark-mode h1.home__name {
  color: #e6e1cd;
}
.dark-mode .ima {
  color: #e6e1cd;
}
.dark-mode .home-animation span::after {
  background-color: #242329;
  border-left: 5px solid #242329;
}
.dark-mode .home__arrow,
.dark-mode .home__fire,
.dark-mode .contact__social-arrow {
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(0%) hue-rotate(137deg) brightness(103%) contrast(102%);
}
.dark-mode .contact__data,
.dark-mode .form {
  background-color: rgb(58, 58, 58);
  -webkit-box-shadow: 0px 34px 65px 19px rgba(138, 76, 76, 0.47);
  -moz-box-shadow: 0px 34px 65px 19px rgba(138, 76, 76, 0.47);
  box-shadow: 0px 34px 65px 19px rgba(138, 76, 76, 0.47);
  border: none;
}
.dark-mode .about {
  background-color: rgba(87, 95, 88, 0.9647058824);
}
.dark-mode .skills__container,
.dark-mode .project__description-info {
  background-color: #524d46;
}
.dark-mode .skills-items:hover {
  background-color: #c9b78f;
}
.dark-mode .contact.section {
  background-color: #242329;
}
.dark-mode .project-description {
  color: rgb(0, 0, 0);
}

.header {
  background-color: #e6e1cd;
  padding: 1rem;
  min-height: auto;
}
@media (min-width: 768px) {
  .header {
    min-height: auto;
    padding: 0.5rem;
  }
}

.nav__logo {
  display: flex;
  align-items: center;
  font-family: "Oswald", sans-serif;
}

.nav__logo-name {
  color: rgb(0, 0, 0);
  font-family: "Oswald", sans-serif;
  font-size: 1.5rem;
  transition: font-size 0.3s ease, font-weight 0.3s;
}
.nav__logo-name:hover {
  font-size: 2rem;
  font-weight: rgb(0, 0, 0);
}

.nav__logo-circle,
.nav__toggle {
  width: 32px;
  height: 32px;
  color: rgb(0, 0, 0);
}

.nav__logo-circle {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Playfair Display", serif;
  border-radius: 50%;
  font-size: 2.3rem;
  font-weight: 700;
  background-color: #d48c8c;
  margin-right: 1rem;
}

.nav__toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 2rem;
  transition: font-size 0.3s ease-in-out;
}
.nav__toggle:hover {
  font-size: 3rem;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 0;
}
@media (min-width: 768px) {
  .nav__bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .nav__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.nav__menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 0.5rem;
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav__menu {
    flex-direction: row;
  }
}

.nav__link {
  color: #e6e1cd;
  margin-bottom: 1rem;
  position: relative;
  transition: color 0.3s;
  font-family: "Oswald", sans-serif;
}
@media (min-width: 768px) {
  .nav__link {
    margin-right: 1rem;
    margin-left: 1rem;
    margin-bottom: 0;
    color: #242329;
  }
}
.nav__link::after {
  content: "";
  width: 0;
  height: 3px;
  position: absolute;
  background-color: rgb(204, 191, 215);
  left: 0;
  bottom: -0.5rem;
  transition: width 0.3s;
}
.nav__link:hover {
  color: #d48c8c;
}
.nav__link:hover::after {
  width: 100%;
}
@media (min-width: 768px) {
  .nav__link:hover {
    color: #d48c8c;
  }
}

.nav__link.contact {
  color: #242329;
  background-color: #d48c8c;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
}
.nav__link.contact:hover {
  background-color: #8a4c4c;
  transform: scale(1.1);
  color: rgb(255, 255, 255);
}
.nav__link.contact::after {
  transition: none;
}

.menu__hidden {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  background-color: rgb(58, 58, 58);
  transition: height 0.5s ease-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 20;
}
.menu__hidden.show {
  height: 32rem;
}
@media (min-width: 768px) {
  .menu__hidden {
    position: relative;
    top: auto;
    background-color: #e6e1cd;
    height: auto;
    opacity: 1;
    display: flex;
  }
  .menu__hidden .nav__close {
    display: none;
  }
  .menu__hidden .menu__title {
    display: none;
  }
}

@media (min-width: 768px) {
  .menu__hidden.show {
    height: auto;
  }
}

.nav__close {
  color: rgb(255, 255, 255);
  font-size: 1.5rem;
  transition: font-size 0.3s ease, color 0.3s;
}
.nav__close:hover {
  font-size: 2rem;
  color: #d48c8c;
}

.dark-mode-button {
  color: #d3591a;
  font-size: 2rem;
  cursor: pointer;
  transition: font-size 0.3s ease;
}
.dark-mode-button:hover {
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .dark-mode-button {
    margin-left: 25px;
  }
}

.menu__title {
  color: rgb(255, 255, 255);
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-family: "Oswald", sans-serif;
  position: relative;
}
.menu__title::before {
  content: "";
  color: rgb(204, 191, 215);
  width: 40px;
  height: 1.5px;
  background-color: rgb(255, 255, 255);
  position: absolute;
  top: 50%;
  left: -4.5rem;
}
.menu__title::after {
  content: "";
  height: 1.5px;
  width: 40px;
  background-color: rgb(255, 255, 255);
  position: absolute;
  top: 50%;
  right: -4.5rem;
}

.home.section {
  margin-top: 0;
}

.home__perfil {
  position: relative;
  justify-self: center;
}

.home__image {
  width: 225px;
  position: relative;
  right: 2rem;
}

.home__img {
  margin-top: 0.5rem;
  position: relative;
  z-index: 10;
}

.home__shadow {
  width: 100%;
  height: 95%;
  background-color: #8a4c4c;
  position: absolute;
  bottom: -2rem;
  right: -2rem;
  border: 4px solid rgb(0, 0, 0);
  z-index: 5;
}

.home__arrow,
.home__fire {
  position: absolute;
}

.home__arrow {
  left: -45%;
  bottom: -5rem;
  transform: rotate(180deg);
  width: 200px;
  z-index: 2;
}

.home__fire {
  left: 80%;
  bottom: 29rem;
  transform: rotate(345deg);
  width: 110px;
  z-index: -1;
}

.contact__social-links__home {
  position: relative;
  right: -22rem;
  top: -25rem;
  width: 8rem;
  height: 10px;
}

.wrapper__home {
  list-style: none;
  width: 100%;
  padding: 10px;
}

.wrapper__home .icon {
  position: relative;
  background: #d48c8c;
  border-radius: 50%;
  margin: 10px;
  width: 40px;
  height: 40px;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  color: rgb(0, 0, 0);
}
.wrapper__home .icon svg {
  width: 35px;
  height: 35px;
}

.wrapper__home .tooltip {
  position: absolute;
  top: 0;
  font-size: 14px;
  background: #fff;
  color: #fff;
  padding: 5px 8px;
  border-radius: 5px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper__home .tooltip::before {
  position: absolute;
  content: "";
  height: 8px;
  width: 8px;
  background: #fff;
  bottom: -3px;
  left: 50%;
  transform: translate(-50%) rotate(45deg);
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper__home .icon:hover .tooltip {
  top: -45px;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.wrapper__home .icon:hover span,
.wrapper__home .icon:hover .tooltip {
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.1);
}

.wrapper__home .linkedin:hover,
.wrapper__home .linkedin:hover .tooltip,
.wrapper__home .linkedin:hover .tooltip::before {
  background: #1877f2;
  color: #fff;
}

.wrapper__home .github:hover,
.wrapper__home .github:hover .tooltip,
.wrapper__home .github:hover .tooltip::before {
  background: rgb(0, 0, 0);
  color: #fff;
}

.home__info {
  margin-top: 1rem;
}

.ima {
  text-align: center;
  margin-bottom: 0;
}

.home__description {
  text-align: center;
  color: rgb(0, 0, 0);
  margin-bottom: 1.5rem;
}

.home__description span {
  font-weight: 700;
}

.majors {
  background: linear-gradient(90deg, rgba(212, 135, 138, 0.6783088235) 10%, rgba(230, 225, 205, 0.7959558824) 59%);
}

.slogan {
  background: linear-gradient(90deg, rgba(230, 225, 205, 0.7959558824) 10%, rgba(212, 135, 138, 0.6783088235) 61%);
}

.download-btn {
  display: block;
  height: 45px;
  width: 105px;
  cursor: pointer;
  background: white;
  border: none;
  background: rgb(0, 0, 0);
  border-radius: 30px;
  overflow: hidden;
  margin: 1rem auto;
}

.button-content {
  transform: translateY(-45px);
  transition: all 250ms ease-in-out;
}

.svg-container,
.text-container {
  height: 45px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-container .text {
  font-size: 13px;
  color: #fff;
  font-weight: 600;
  opacity: 1;
  transition: opacity ease-in-out 250ms;
}

.download-icon {
  height: 25px;
  width: 25px;
  fill: #d48c8c;
  opacity: 0;
  transition: opacity ease-in-out 250ms;
}

/* hover state for the button */
.download-btn:hover .button-content {
  transform: translateY(0px);
}

.download-btn:hover .text {
  opacity: 0;
}

.download-btn:hover .download-icon {
  opacity: 1;
}

.download-btn:focus .download-icon {
  -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
  animation: heartbeat 1.5s ease-in-out infinite both;
}

@-webkit-keyframes heartbeat {
  from {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: center center;
    transform-origin: center center;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  10% {
    -webkit-transform: scale(0.91);
    transform: scale(0.91);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  17% {
    -webkit-transform: scale(0.98);
    transform: scale(0.98);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  33% {
    -webkit-transform: scale(0.87);
    transform: scale(0.87);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  45% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}
@keyframes heartbeat {
  from {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: center center;
    transform-origin: center center;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  10% {
    -webkit-transform: scale(0.91);
    transform: scale(0.91);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  17% {
    -webkit-transform: scale(0.98);
    transform: scale(0.98);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  33% {
    -webkit-transform: scale(0.87);
    transform: scale(0.87);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  45% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}
@media (min-width: 768px) {
  .home__container {
    height: 500px;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
  }
  .home__container .home__perfil {
    position: relative;
    top: 5rem;
  }
  .home__container .home__name {
    position: relative;
    top: 5rem;
  }
  .home__container .home__arrow {
    top: -10rem;
    left: -10rem;
  }
  .home__container .home__info {
    position: relative;
    top: -30rem;
    margin: 0 auto;
    text-align: center;
  }
  .home__container .home__scroll {
    margin: 0;
  }
