}

.timeline-items::before {
  content: "";
  position: absolute;
  width: 5px;
  height: 100%;
  left: calc(50% - 1px);
  background-color: #d48c8c;
}

.timeline-item {
  position: relative;
  width: 100%;
  margin-bottom: 5rem;
}

@media (min-width: 768px) {
  .timeline-item:nth-child(odd) {
    padding-right: calc(50% + 30px);
