    text-align: right;
  }
  .timeline-item:nth-child(even) {
    padding-left: calc(50% + 30px);
    text-align: left;
  }
}
.timeline-dot {
  height: 21px;
  width: 21px;
  background-color: #d48c8c;
  box-shadow: 0 0 25px #d48c8c, 0 0 50px #d48c8c;
