.slide-right {
  -webkit-animation: slide-right 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-right 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@-webkit-keyframes slide-right {
  0% {
    -webkit-transform: translateX(-100px);
    transform: translateX(-100px);
  }
  100% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }
}
@keyframes slide-right {
  0% {
    -webkit-transform: translateX(-100px);
    transform: translateX(-100px);
  }
  100% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }
}
.slide-left {
  -webkit-animation: slide-left 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-left 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@-webkit-keyframes slide-left {
  0% {
    -webkit-transform: translateX(100px);
    transform: translateX(100px);
  }
  100% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }
}
@keyframes slide-left {
  0% {
    -webkit-transform: translateX(100px);
    transform: translateX(100px);
  }
  100% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }
}
.shadow-drop-2-center {
  -webkit-animation: shadow-drop-2-center 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: shadow-drop-2-center 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.home-animation {
  text-align: center;
}
.home-animation span {
  position: relative;
}

.home-animation span::before {
  content: "Systems Engineer";
  color: #d48c8c;
  animation: words 20s infinite;
}

.home-animation span::after {
  content: "";
  background-color: #e6e1cd;
  position: absolute;
  width: calc(100% + 8px);
  height: 100%;
  border-left: 5px solid #e6e1cd;
  right: -5px;
  animation: typing 20s steps(14) infinite;
}

@keyframes words {
  0%, 20% {
    content: "Systems Engineer";
  }
  21%, 40% {
    content: "Chemical Engineer";
  }
  41%, 60% {
    content: "Data Analyst";
  }
  61%, 80% {
    content: "Web Developer";
  }
  81%, 100% {
    content: "Leader";
  }
}
@keyframes typing {
  10%, 15%, 30%, 35%, 50%, 55%, 70%, 75%, 90%, 95% {
    width: 0;
  }
  5%, 20%, 25%, 40%, 45%, 60%, 65%, 80%, 85% {
    width: calc(100% + 10px);
  }
}
